export default {
	props: ["tabbarText"],
	data() {
		return {
			tbt: this.tabbarText
		};
	},
	onLoad() {
	},
	methods: {
		changeActive: function(e){
			var that = this
			let id = e.target.id
			var tbt = this.tbt;
			for(var key in tbt){
				if(key == id){
					tbt[key].status = "active"
				} else {
					tbt[key].status = "unactive"
				}
			}
			that.$emit("listenTabbar", id)
		}
	}
}
